"use client";

import { useState } from "react";

export default function AddProductPage() {
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    price: "",
    imageUrl: "",
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // এখন শুধু console.log করবো, DB সংযোগ পরে যোগ করা যাবে
    console.log("Product Data:", formData);
    alert("Product data ready to submit (mock)");
    setFormData({ name: "", description: "", price: "", imageUrl: "" });
  };

  return (
    <div className="max-w-3xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6 text-center">পণ্য যোগ করুন</h1>

      <form
        onSubmit={handleSubmit}
        className="bg-white shadow-md rounded-lg p-6 space-y-4"
      >
        {/* Name */}
        <div>
          <label className="block font-semibold mb-1">পণ্যের নাম</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="পণ্যের নাম লিখুন"
            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
            required
          />
        </div>

        {/* Description */}
        <div>
          <label className="block font-semibold mb-1">বর্ণনা</label>
          <textarea
            name="description"
            value={formData.description}
            onChange={handleChange}
            placeholder="পণ্যের বর্ণনা লিখুন"
            className="w-full border border-gray-300 rounded px-3 py-2 h-24 resize-none focus:outline-none focus:ring-2 focus:ring-blue-400"
            required
          />
        </div>

        {/* Price */}
        <div>
          <label className="block font-semibold mb-1">মূল্য (৳)</label>
          <input
            type="number"
            name="price"
            value={formData.price}
            onChange={handleChange}
            placeholder="পণ্যের দাম লিখুন"
            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
            required
          />
        </div>

        {/* Image URL */}
        <div>
          <label className="block font-semibold mb-1">ছবির URL</label>
          <input
            type="text"
            name="imageUrl"
            value={formData.imageUrl}
            onChange={handleChange}
            placeholder="পণ্যের ছবি URL দিন"
            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
          />
        </div>

        {/* Submit Button */}
        <button
          type="submit"
          className="w-full bg-blue-500 text-white font-semibold py-2 rounded hover:bg-blue-600 transition"
        >
          পণ্য যোগ করুন
        </button>
      </form>
    </div>
  );
}
